<template>
    <!-- 
        el-tree 
            show-checkbox  	boolean		false  
            props		object
                label		string, function(data, node)		
                children		string		
                disabled		boolean, function(data, node)		
                isLeaf	 lazy 	boolean, function(data, node)
            load	 lazy true 	function(node, resolve)
            lazy	 load 	boolean		false
     -->
    <el-tree :props="props" :load="loadNode" lazy @node-click="clickHandle" accordion>
    </el-tree>

</template>

<script>
export default {
    data() {
        return {
            props: {
                label: 'name',
                children: 'zones',
                isLeaf: 'leaf'
            },
            treeData: []
        };
    },
    methods: {
        //tree--------------------
        clickHandle(data,node){
            // console.log('data',data,'node',node);
            this.$emit('sendTreeData',data)
        },

        //tree---
        loadNode(node, resolve) {
            // console.log('node--', node);
            if (node.level === 0) {
                this.$api.selectItemCategoryByParentId()
                    .then(res => {
                        if (res.data.status == 200) {
                            return resolve(res.data.result);
                        }else{
                            return resolve([])
                        }
                    })
            }
            if (node.level >= 1){
                //--------
                this.$api.selectItemCategoryByParentId({type:node.data.cid})
                    .then(res => {
                        if (res.data.status == 200) {
                            return resolve(res.data.result);
                        }else{
                            return resolve([])
                        }
                    })
            } 
        },
        //tree----------------------
        // async selectItemCategoryByParentId(type) {
        //     let res = await this.$api.selectItemCategoryByParentId({ type })
        //     console.log('tree------', res.data);
        // }
    }
};
</script>

