<template>
    <el-row :gutter="20">
        <el-col :span="4">
            <div class="nav">
                <div class="title"></div>

                <div class="tree">
                    <TreeProduct @sendTreeData="sendTreeData"></TreeProduct>
                </div>
            </div>
        </el-col>
        <el-col :span="20">
            <div class="wrapper">
                <div class="my-title">{{ title }}</div>
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="" prop="category">
                        <span>{{ ruleForm.category }}</span>
                    </el-form-item>
                    <el-form-item label="" prop="title">
                        <el-input v-model="ruleForm.title" size="small"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="price">
                        <el-input v-model="ruleForm.price" size="small"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="num">
                        <el-input v-model="ruleForm.num" size="small"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="sellPoint">
                        <el-input v-model="ruleForm.sellPoint" size="small"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="image">
                        <UploadImg @sendImg="sendImg" ref="uploadImg" :fileList="fileList"></UploadImg>
                    </el-form-item>
                    <el-form-item label="" prop="descs">
                        <WangEditor @sendWangEditor="sendWangEditor" ref="wangEditor"></WangEditor>
                    </el-form-item>
                    <el-form-item label="" prop="isShow">
                        <el-switch v-model="ruleForm.isShow" active-color="#13ce66"
                            inactive-color="#ff4949"></el-switch>
                    </el-form-item>
                    <el-form-item label="" prop="isShow">
                        <el-switch v-model="ruleForm.isShow" active-color="#13ce66"
                            inactive-color="#ff4949"></el-switch>
                    </el-form-item>
                    <el-form-item label="" prop="isShow">
                        <el-switch v-model="ruleForm.isShow" active-color="#13ce66"
                            inactive-color="#ff4949"></el-switch>
                    </el-form-item>

                    <el-form-item>
                        <el-button v-show="title!=''" type="primary" @click="submitForm('ruleForm')"></el-button>
                        <el-button v-show="title!=''" @click="resetForm('ruleForm')"></el-button>
                        <el-button @click="closePage"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-col>
    </el-row>
</template>

<script>
import { mapState } from 'vuex'
import TreeProduct from './TreeProduct.vue'
import UploadImg from './UploadImg.vue'
import WangEditor from './WangEditor.vue'

export default {
    components: {
        TreeProduct,
        UploadImg,
        WangEditor
    },
    computed: {
        ...mapState('product', ['rowData', 'title'])
    },
    data() {
        return {
            fileList: [],//
            ruleForm: {//
                id: '',
                title: '',
                num: '',
                price: '',
                sellPoint: '',
                image: [],
                descs: '',
                category: '',
                cid: '',
                isShow: true,
            },
            rules: {//
                title: [
                    { required: true, message: '', trigger: 'blur' },
                    // { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
                ],
                num: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                price: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                category: [{
                    required: true, message: '', trigger: 'blur'
                }]
            }
        };
    },
    methods: {
        //
        closePage(){
            this.$router.push('/product/list')
        },
        //tree--------------------------
        sendTreeData(val) {
            //tree val={id:,cid:,name:''}
            this.ruleForm.category = val.name
            this.ruleForm.cid = val.cid
        },
        //----------------------
        sendImg(val) {
            // console.log('val-----',val);
            this.ruleForm.image.push(val)
        },
        //---------------------------
        sendWangEditor(val) {
            console.log('-----', val);
            this.ruleForm.descs = val;
        },
        //-------------------------------
        submitForm(formName) {
            //this.$refs.ruleForm
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    //title, image, sellPoint, price, cid, category, num, descs
                    let { id, title, image, sellPoint, price, cid, category, num, descs } = this.ruleForm
                    if (this.title == '') {
                        console.log('', this.ruleForm, image);
                        //image------
                        this.insertTbItem({ title, image: JSON.stringify(image), sellPoint, price, cid, category, num, descs })
                    } else {
                        console.log('---', this.ruleForm, '-------------', image);
                        this.updateTbItem({ id, title, image: JSON.stringify(image), sellPoint, price, cid, category, num, descs })
                    }

                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        //----------------------------------
        async updateTbItem(params) {
            let res = await this.$api.updateTbItem(params)
            console.log('', res.data);
            if (res.data.status === 200) {
                this.$message({
                    message: '',
                    type: 'success'
                });
                //
                this.$router.push('/product/list')
            }
        },
        //---------------------------
        async insertTbItem(params) {
            let res = await this.$api.insertTbItem(params)
            console.log('--------', res.data);
            if (res.data.status === 200) {
                this.$message({
                    message: '',
                    type: 'success'
                });
                //
                this.$router.push('/product/list')
            }
        },
        //------------------------------
        resetForm(formName) {
            if (this.title == '') {
                this.$refs[formName].resetFields();
                //
                this.$refs.uploadImg.clear();
                //wangEditor 
                this.$refs.wangEditor.html = ''//1
                // this.$refs.wangEditor.editor.clear();//2 
            } else {
                //----------------
                //1.   2. vuex  rowData--rowData
                console.log('---this.ruleForm---', this.ruleForm);
                console.log('---this.rowData---', this.rowData);
                let rowData=JSON.parse(JSON.stringify(this.rowData))
                this.ruleForm = rowData
                this.ruleForm.isShow = true;
                let imgs = rowData.image;//--- 
                let arr = JSON.parse(imgs);//
                console.log('---arr---',arr);
                this.ruleForm.image = arr;
                // console.log('arr---', arr);
                this.fileList=[];
                arr.forEach(ele => {
                    this.fileList.push({ name: '', url: ele });//fileList --UploadImg
                });
                this.$nextTick(() => {
                this.$refs.wangEditor.html = rowData.descs;
            })
            }


        }
    },
    mounted() {
        //--
        console.log('created--------------');
        if (this.title === ''|| this.title==='' ) {
            //rowData  JSON.parse(JSON.stringify())
            // this.ruleForm = { ...this.rowData };//image 
            this.ruleForm = JSON.parse(JSON.stringify(this.rowData)) //image
            this.ruleForm.isShow = true;
            let imgs = this.ruleForm.image;//--- 
            let arr = JSON.parse(imgs);//
            this.ruleForm.image = arr;
            console.log('arr---', arr);
            arr.forEach(ele => {
                this.fileList.push({ name: '', url: ele });//fileList --UploadImg
            });
            console.log('this.fileList---', this.fileList);
            //--------------
            console.log('wangEditor--------------', this.$refs.wangEditor, this.$refs.wangEditor.html);//undefined
            //this.$nextTick() DOM 
            this.$nextTick(() => {
                this.$refs.wangEditor.html = this.rowData.descs;
            })
        }
    }
}
</script>

<style lang="less" scoped>
.nav {
    height: 600px;
    background: #fff;

    .title {
        background: skyblue;
        padding: 20px;
        color: #fff;
    }

    .tree {
        padding: 20px;
    }
}

.wrapper {
    // height: 800px;
    background: #fff;

    .my-title {
        background: #eee;
        padding: 14px;
        color: #333;
        font-weight: bold;
    }

    .demo-ruleForm {
        padding: 20px;
    }
}
</style>