<template>
    <div>
        <!-- 
            el-upload 
                action		string
                list-type		string	text/picture/picture-card	text
                on-preview		function(file)		
                on-remove		function(file, fileList)		
                on-success		function(response, file, fileList)
                multiple		boolean
                file-list	, : [{name: 'food.jpg', url: 'https://xxx.cdn.com/xxx.jpg'}]	array		[]
                disabled		boolean		false
                limit		number		
                on-exceed		function(files, fileList)

                
                    clearFiles
      
         -->
        <el-upload 
            :action="uploadUrl" 
            list-type="picture-card"
            :on-preview="handlePictureCardPreview" 
            :on-remove="handleRemove"
            :on-success="handleSuccess"
            :file-list="fileList"
            ref="upload"
            multiple>
            <i class="el-icon-plus"></i>
        </el-upload>
    </div>
</template>

<script>
import {uploadUrl,host} from '@/api/base'
export default {
    props:{
        fileList:{
            type:Array,
            default:function(){
                return []
            }
        }
    },
    data() {
        return {
            uploadUrl,
            // fileList:[],
            // [{name: 'food.jpg', url: 'https://xxx.cdn.com/xxx.jpg'}]
        };
    },
    methods: {
        //-----------------------------
        handleSuccess(response, file, fileList){
            console.log('',response, file, fileList);
            // <img src=''/> http://localhost:7788/+
            let url = response.url.slice(7);
            let imgUrl = host+'/'+url
            // console.log('url',imgUrl);
            this.$emit('sendImg',imgUrl)
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePictureCardPreview(file) {
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
        },
        //---------------
        clear(){
            // console.log('-------');
            // this.fileList=[]
            this.$refs.upload.clearFiles();
        }
    }
}
</script>

